"use client";

export default function ProfilePage() {
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  if (!token) {
    if (typeof window !== "undefined") window.location.href = "/login";
    return null;
  }

  const handleLogout = () => {
    localStorage.removeItem("token");
    window.location.href = "/login";
  };

  return (
    <div className="card p-6">
      <h1 className="text-2xl font-bold text-purple-700 mb-4">Your Profile</h1>
      <p className="text-gray-700 mb-2">This is a simple profile screen. You can enhance it later to fetch user info from the backend.</p>
      <button className="btn btn-red" onClick={handleLogout}>Logout</button>
    </div>
  );
}

